* {
  margin: 0;
  padding: 0;
}

.container {
  flex-direction: column;
  margin: 100px 50px 50px;
  font-family: sans-serif;
  display: flex;
}

.content {
  width: 204px;
  background-color: #a5d8d8;
  border: 5px solid #000;
  border-radius: 24px;
  margin: 20px auto;
  font-size: 0;
}

.item {
  width: 50px;
  height: 50px;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: gray;
  border: 1px solid #000;
  border-radius: 15px;
  flex-wrap: wrap;
  padding: 8px;
  font-size: 26px;
  line-height: 50px;
  transition: background .1s;
  display: inline-block;
}

.item:hover {
  background-color: #57dee5;
}

.winning-cell {
  background-color: #8087e9;
}

.btnrestart {
  width: 200px;
  cursor: pointer;
  text-align: center;
  color: #a5d8d8;
  background-color: gray;
  border: 5px solid #000;
  border-radius: 15px;
  margin: 0 auto 10px;
  padding: 8px;
  font-size: 20px;
  display: inline-block;
}

.currentPlayer {
  width: 150px;
  text-align: center;
  color: #a5d8d8;
  background-color: gray;
  border: 5px solid #000;
  border-radius: 15px;
  align-items: center;
  margin: 0 auto;
  padding: 5px;
  font-size: 18px;
  display: flex;
}

.stat {
  width: 200px;
  background-color: gray;
  border: 5px solid #000;
  border-radius: 15px;
  align-items: center;
  margin: 20px auto;
  font-size: 18px;
  display: flex;
}

.stat table {
  border-collapse: collapse;
}

.stat table, .stat th, .stat td {
  width: 200px;
  text-align: center;
  color: #a5d8d8;
  border: 1px solid #000;
  padding: 5px;
}

input {
  color: #1c2424;
  background-color: #a5d8d8;
  border-radius: 10px;
  padding: 5px;
}

